import React, { useContext } from 'react'
import { AppContext } from "../context/general.context";
import "./Image.css"

function Image() {

    const { 
        isLoading, setIsLoading, 
        user, setUser,
        fetchingData, setFetchingData,
        image, setImage,
        textDescription
      }  = useContext(AppContext)
    

  return (
    <div id='flexin'>
        <img id='listImage' src={image}></img>
        <p>{textDescription}</p>
    </div>
  )
}

export default Image