import Form from 'react-bootstrap/Form';
import React, { useContext } from 'react'
import { AppContext } from "../context/general.context";
import "./Form.css"
import {  Spinner, Button } from "react-bootstrap";
import axios from "axios";
import Image from './Image';


function FormTextExample({generate}) {



  
  const { 
    fetchingData, setImage,
    setTextDescription
 }  = useContext(AppContext)

  if(fetchingData){
    return <Spinner/>
  }

  return (
    <div id='form'>

      
			<div id="header">
				<div class="box alt container">
						<section>
						<header>
              <Image></Image>
            <h1 id='cool'>Text to Image AI Generator</h1>
            

						
						</header>
						<form onSubmit={generate}>
							<div class="row">

								<div class="col-6 col-12-mobilep">
								
									<select id="size" name="cars">
										
										<option value="small">Small</option>
										<option value="medium">Medium</option>
										<option value="large">Large</option>
									</select>
								</div>
								<div class="col-12">
									<label for="subject">Descripe your desired image:</label> <br></br>
                  <label for="subject"><i>(you can be very thorough and specific)</i></label>
									<input class="text" type="text" name="prompt" id="subject" placeholder="Enter your subject" />
								</div>
								<div class="col-12">
									<ul class="actions special">
                  <Button type='submit' id='hovering' variant="dark">Generate</Button >
									
									</ul>
								</div>
							</div>
						</form>
					</section>
				</div>
			</div>
       
       

    </div>
  );
}

export default FormTextExample;