import React, { useContext } from 'react'
import { AppContext } from "../context/general.context";
import "./List.css"

function List() {


    const { 
        imageList
     }  = useContext(AppContext)
    
console.log(imageList, "hey hey alkjflaskdklsa")


  return (
    <div className='flexin'>
     <h1>hey</h1>
{imageList.map((item)=>{

    return (
        <div key={item._id}>
        <img className='list' src={item.imageUrl}></img>
        <p className=''>{item.prompt}</p>

        </div>
    )
})
}

    </div>
  )
}

export default List