import { createContext, useState, useEffect } from "react";
import axios from "axios";

let AppContext = createContext()

function AppContextWrapper(props){

    
    const [user, setUser] = useState(null);
    const [isLoading, setIsLoading] = useState();
    const [fetchingData, setFetchingData] = useState(false);
    const [textDescription, setTextDescription] = useState("");
    const [image, setImage ] = useState("pngwing.png")
    const [imageList, setImageList] = useState([])
    const [editImage, setEditImage] = useState("pngwing.png")
   


    return (
        <AppContext.Provider value={{
         user, setUser,
         isLoading, setIsLoading, 
         fetchingData, setFetchingData,
         image, setImage,
         textDescription, setTextDescription,
         imageList, setImageList,
         editImage, setEditImage
        
         
         }}>
            {props.children}
        </AppContext.Provider>
    )
}

export {AppContext, AppContextWrapper}